import chroma from 'chroma-js';
import sizes from './sizes';

const styles = {
    root: {
        width: "20%",
        height: "25%",
        margin: "0 auto",
        display: "inline-block",
        position: "relative",
        cursor: "pointer",
        marginBottom: "-3.5px",
        marginTop: "-1.7px",
        "&:hover svg": {
            color: "white",
            transform: "scale(1.2)"
        },
        [sizes.down('lg')] : {
            width: "25%",
            height: "20%"
        },
        [sizes.down('md')]: {
            width: "50%",
            height: "10%"
        },
        [sizes.down('sm')]: {
            width: "100%",
            height: "5%",
        }
    },
    boxContent: {
        position: "absolute",
        width: "100%",
        padding: "10px",
        left: "0",
        bottom: "0",
        letterSpacing: "1px",
        textTransform: "uppercase",
        fontSize: "12px",
        display: "flex",
        justifyContent: "space-between",
        color: props => chroma(props.color).luminance() <= 0.08 ? "white" : "black",
        [sizes.down('sm')]: {
            padding: 0
        }
    },
    DeleteIcon: {
        transition: "all 0.3s ease-in-out",
        [sizes.down('sm')]: {
            fontSize: "1rem",
        }
    }
}

export default styles;